import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
// 引入类型信息组件
import addCode from '@/views/main/CodeConfiguration/addCode.vue';
import {
  mainCodeGetAllAction,
  mainCodeUpdateByIdAction,
  mainCodeDeleteByIdsAction
} from '@/api/management';
import { STATUS_CODE, TITLE, MSG, VARIABLE, BTN_TEXT, CONSTANTS_CODE } from '@/utils/constantsFile';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';

export default {
  name: 'CodeConfiguration',
  data() {
    return {
      CONSTANTS_CODE: CONSTANTS_CODE,
      BTN_TEXT: BTN_TEXT,
      VARIABLE: VARIABLE,
      showPage: 0,
      deviceChecks: [],
      // 对话框标题
      titleName: CONSTANTS_CODE.TITLE_MODIFY_CODE_DIALOG,
      // 保存当前选中行修改前的数据
      initRow: null,
      // 暂存修改后的row数据
      tempRow: {
        id: 0,
        codeName: '', // 编码名称
        model: '', // 模块
        prefix: '', // 固定字符（前缀）
        suffix: '', // 固定字符（后缀）
        year: null, // 年
        haveMonth: null, // 月
        haveDay: null, // 日
        seed: 1, // 变量开始值
        step: 1, // 步长
        pipelineLength: 4 // 流水长度
      },
      yearOptions: [
        {
          id: 1,
          label: VARIABLE.NOT_HAVE,
          value: 0
        },
        {
          id: 2,
          label: '2' + VARIABLE.PLACE,
          value: 1
        },
        {
          id: 3,
          label: '4' + VARIABLE.PLACE,
          value: 2
        }
      ],
      monthOrDayOptions: [
        {
          id: 0,
          label: VARIABLE.NOT_HAVE,
          value: false
        },
        {
          id: 1,
          label: VARIABLE.HAVE,
          value: true
        }
      ],
      // 表单校验规则
      codeRules: {
        codeName: [
          {
            required: true,
            trigger: 'blur'
          }
        ],
        model: [
          {
            required: true,
            trigger: 'blur'
          }
        ],
        prefix: [
          {
            required: true,
            trigger: 'blur',
            message: CONSTANTS_CODE.PREFIX_VALIDATION_FAILED
          },
          {
            validator: this.checkPrefix,
            trigger: 'blur'
          }
        ],
        suffix: [
          {
            required: true,
            trigger: 'blur',
            message: CONSTANTS_CODE.SUFFIX_VALIDATION_FAILED
          },
          {
            validator: this.checkSuffix,
            trigger: 'blur'
          }
        ],
        seed: [
          {
            required: true,
            type: 'number',
            trigger: 'blur',
            message: CONSTANTS_CODE.SEED_VALIDATION_FAILED
          }
        ],
        pipelineLength: [
          {
            required: true,
            type: 'number',
            trigger: 'blur',
            message: CONSTANTS_CODE.PIPELINELENGTH_VALIDATION_FAILED
          }
        ],
        step: [
          {
            required: true,
            type: 'number',
            trigger: 'blur',
            message: CONSTANTS_CODE.STEP_VALIDATION_FAILED
          }
        ],
        yearLength: [
          {
            required: true,
            trigger: 'change',
            message: CONSTANTS_CODE.YEAR_VALIDATION_FAILED
          }
        ],
        haveMonth: [
          {
            required: true,
            trigger: 'change',
            message: CONSTANTS_CODE.MONTH_VALIDATION_FAILED
          }
        ],
        haveDay: [
          {
            required: true,
            trigger: 'change',
            message: CONSTANTS_CODE.DAY_VALIDATION_FAILED
          }
        ]
      },

      tableLoading: false,

      // 修改对话框是否可见，默认为false，表示不可见
      dialogVisible: false,
      formLabelWidth: '140px',

      // 表格初始化用到的数据
      tableDatas: [],

      // 表格分页的配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [5, 10, 20, 30, 50],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },

      // 表格列配置选项
      tableColumns: [
        {
          type: 'radio',
          align: 'center',
          visible: true,
          width: '50'
        },
        // { type: 'checkbox', width: '50', align: 'center' },
        {
          field: 'codeName',
          title: CONSTANTS_CODE.CODE_NAME,
          minWidth: '150',
          align: 'center'
        },
        {
          field: 'model',
          title: CONSTANTS_CODE.MODEL,
          minWidth: '140',
          align: 'center'
        },
        {
          field: 'prefix',
          title: CONSTANTS_CODE.PREFIX,
          width: '160',
          align: 'center'
        },
        {
          field: 'yearLength',
          title: CONSTANTS_CODE.YEAR,
          width: '80',
          align: 'center',
          formatter: this.formatterYearLength
        },
        {
          field: 'haveMonth',
          title: CONSTANTS_CODE.MONTH,
          width: '80',
          align: 'center',
          formatter: this.formatterHaveOrNot
        },
        {
          field: 'haveDay',
          title: CONSTANTS_CODE.DAY,
          width: '80',
          align: 'center',
          formatter: this.formatterHaveOrNot
        },
        {
          field: 'seed',
          title: CONSTANTS_CODE.SEED,
          minWidth: '130',
          align: 'center'
        },
        {
          field: 'step',
          title: CONSTANTS_CODE.STEP,
          minWidth: '90',
          align: 'center'
        },
        {
          field: 'suffix',
          title: CONSTANTS_CODE.SUFFIX,
          width: '160px',
          align: 'center'
        },
        {
          field: 'pipelineLength',
          title: CONSTANTS_CODE.PIPE_LINE_LENGTH,
          width: '120',
          align: 'center'
        }
      ]
    };
  },

  // 使用的组件
  components: {
    HeaderTitle,
    HeadOperationItem,
    VXETable,
    addCode
  },

  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: 点击批量删除
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    deteleall() {
      if (this.tableChecks.length === 0) {
        this.$message.error(MSG.PLEASE_CHOOSE_DELETE);
      } else {
        var arrdelete = [];
        this.tableChecks.map(item => arrdelete.push(item.codeId));
        this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
          confirmButtonText: BTN_TEXT.CONFIRM,
          cancelButtonText: BTN_TEXT.CANCEL,
          type: 'warning'
        })
          .then(() => {
            this.setDeleteChecks(arrdelete);
          })
          .catch(() => {
          });
      }
    },
    /**
     * @description: 执行批量删除的方法
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async setDeleteChecks(param) {
      try {
        const res = await mainCodeDeleteByIdsAction({
          ids: param
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.findTableList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取批量勾选的数据
     * @param data // 勾选数据
     * @return: 无
     * @author: ZJM
     */
    tableChooseCheck(data) {
      this.tableChecks = data;
    },
    toDelete() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      // 确认是否删除
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setDeleteChecks([row.codeId]);
        })
        .catch(() => { });
    },
    /**
     * @description: 点击“修改”按钮触发的对话框弹出事件
     * @param {object} row 行对象
     * @param {object} column 列对象
     * @return: null
     */
    showDialog() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      this.dialogVisible = true;

      // 保存row修改过的数据
      this.tempRow = JSON.parse(JSON.stringify(row));

      // 保存row的初始数据，用于重置
      this.initRow = row;
    },

    /**
     * @description: 校验前缀字符串的方法
     * @param {object} rule 校验规则
     * @param {string} value input输入值
     * @param {function} callback 回调函数
     * @return: void
     */
    checkPrefix(rule, value, callback) {
      if (value && !/^[a-zA-Z&#$@*-_]{1,6}$/.test(value)) {
        callback(new Error(CONSTANTS_CODE.PREFIX_VALIDATION_FAILED));
      }
      callback();
    },

    /**
     * @description: 校验后缀字符串的方法
     * @param {object} rule 校验规则
     * @param {string} value input输入值
     * @param {function} callback 回调函数
     * @return: void
     */
    checkSuffix(rule, value, callback) {
      if (value && !/^[a-zA-Z&#$@*-_]{1,6}$/.test(value)) {
        callback(new Error(CONSTANTS_CODE.SUFFIX_VALIDATION_FAILED));
      }
      callback();
    },

    /**
     * @description: 提交编辑后的行数据
     * @param {null} 无
     * @return: null
     */
    async submitEdit() {
      try {
        this.$refs.editCodeForm.validate((valid) => {
          if (!valid) {
            throw new Error(this.GLOBAL.MSG.VALIDATION_FAILED);
          }
        });

        const form = JSON.parse(JSON.stringify(this.tempRow));
        delete form.createTime;
        delete form.updateTime;
        // 获取响应结果
        const res = await mainCodeUpdateByIdAction(form);

        // 响应为true，表示修改提交成功
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 弹出消息提示
          showSuccessMessage(res.message);

          // 隐藏对话框
          this.dialogVisible = false;
          this.initRow = null;

          // 调用表格初始化方法，获取修改后的数据
          this.findTableList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        this.$notify.error({
          title: TITLE.ERROR,
          message: error,
          showClose: false,
          duration: 2000,
          position: 'bottom-right'
        });
      }
    },

    /**
     * @description: 重置编辑，恢复最初的数据
     * @param {null} null
     * @return: null
     */
    resetEdit() {
      this.tempRow = { ...this.initRow };
      this.$refs.editCodeForm.resetFields();
    },

    /**
     * @description: （修改编码对话框）“取消”按钮点击事件
     * @param 无
     * @return: void
     */
    cancelBtnClick() {
      this.$refs.editCodeForm.resetFields();
      this.dialogVisible = false;
    },

    /**
     * @description: 获取表格数据
     * @param {null} 无
     * @return: void
     */
    async findTableList() {
      this.tableChecks = [];
      try {
        // 播放表格加载动画
        this.tableLoading = true;
        const data = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };
        const res = await mainCodeGetAllAction(data);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },

    /**
     * @description: 分页处理方法
     * @param {number} currentPage 当前页码
     * @param {number} pageSize 每页行数
     * @return: void
     */
    onTablePageChange({ currentPage, pageSize }) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.findTableList();
    },

    /**
     * @description: month和day的格式化处理办法，0代表‘无’，1代表‘有’
     * @param {number} cellValue 单元格的值
     * @return: string
     */
    handleTimeFormat({ cellValue }) {
      switch (cellValue) {
        case CONSTANTS_CODE.NOT_HAVE:
          return VARIABLE.HAVE;
        case CONSTANTS_CODE.HAVE:
          return VARIABLE.NOT_HAVE;
        default:
          return '';
      }
    },
    /**
     * @description: 切换组件页面
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async togglePage() {
      this.showPage = 1;
    },
    /**
     * @description: 切换主页面
     * @param val 状态
     * @return: 无
     * @author: ZJM
     */
    goBack(val) {
      this.showPage = val;
      this.findTableList();
    },
    formatterYearLength({ cellValue }) {
      if (cellValue === 0) {
        return VARIABLE.NOT_HAVE;
      } else if (cellValue === 1) {
        return '2' + VARIABLE.PLACE;
      } else if (cellValue === 2) {
        return '4' + VARIABLE.PLACE;
      }
    },
    formatterHaveOrNot({ cellValue }) {
      if (cellValue === false) {
        return VARIABLE.NOT_HAVE;
      } else if (cellValue === true) {
        return VARIABLE.HAVE;
      }
    },
    refreshTable() {
      this.findTableList();
    }
  },
  created() {
    this.findTableList();
  }
};
