// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 导入所需接口
import {
  mainCodeInsertBatchAction
} from '@/api/management';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { TITLE, STATUS_CODE, MSG, VARIABLE, BTN_TEXT, CONSTANTS_CODE } from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem
  },
  props: {
  },
  data() {
    // 验证前缀
    const prefixValid = ({ cellValue }) => {
      return new Promise((resolve, reject) => {
        if (cellValue && !/^[a-zA-Z&#$@*-_]{1,6}$/.test(cellValue)) {
          reject(new Error(CONSTANTS_CODE.PREFIX_VALIDATION_FAILED));
        }
        resolve();
      });
    };

    // 验证后缀
    const suffixValid = ({ cellValue }) => {
      return new Promise((resolve, reject) => {
        if (cellValue && !/^[a-zA-Z&#$@*-_]{1,6}$/.test(cellValue)) {
          reject(new Error(CONSTANTS_CODE.SUFFIX_VALIDATION_FAILED));
        }
        resolve();
      });
    };

    return {
      BTN_TEXT: BTN_TEXT,
      imageUrl: '',
      // 页面标题
      titleName: CONSTANTS_CODE.ADD_CODE,
      // 表单校验规则
      codeRules: {
        codeName: [
          {
            required: true,
            trigger: 'blur',
            message: CONSTANTS_CODE.PLEASE_ENTER_CONTENT
          }
        ],
        model: [
          {
            required: true,
            trigger: 'blur',
            message: CONSTANTS_CODE.PLEASE_ENTER_CONTENT
          }
        ],
        prefix: [
          { validator: prefixValid },
          {
            required: true,
            trigger: 'blur',
            message: CONSTANTS_CODE.PLEASE_ENTER_CONTENT
          }
        ],
        suffix: [
          { validator: suffixValid },
          {
            required: true,
            trigger: 'blur',
            message: CONSTANTS_CODE.PLEASE_ENTER_CONTENT
          }
        ],
        seed: [
          {
            required: true,
            type: 'number',
            trigger: 'blur',
            message: CONSTANTS_CODE.SEED_VALIDATION_FAILED
          }
        ],
        pipelineLength: [
          {
            required: true,
            type: 'number',
            trigger: 'blur',
            message: CONSTANTS_CODE.PIPELINELENGTH_VALIDATION_FAILED
          }
        ],
        step: [
          {
            required: true,
            type: 'number',
            trigger: 'blur',
            message: CONSTANTS_CODE.STEP_VALIDATION_FAILED
          }
        ],
        yearLength: [
          {
            required: true,
            trigger: 'change',
            message: CONSTANTS_CODE.YEAR_VALIDATION_FAILED
          }
        ],
        haveMonth: [
          {
            required: true,
            trigger: 'change',
            message: CONSTANTS_CODE.MONTH_VALIDATION_FAILED
          }
        ],
        haveDay: [
          {
            required: true,
            trigger: 'change',
            message: CONSTANTS_CODE.DAY_VALIDATION_FAILED
          }
        ]
      },
      // 可编辑配置
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: true
        // icon: 'fa fa-file-text-o'
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'codeName',
          title: CONSTANTS_CODE.CODE_NAME,
          minWidth: '150',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'model',
          title: CONSTANTS_CODE.MODEL,
          minWidth: '140',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'prefix',
          title: CONSTANTS_CODE.PREFIX,
          width: '160',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'yearLength',
          title: CONSTANTS_CODE.YEAR,
          width: '80',
          align: 'center',
          editRender: {
            name: '$select',
            options: [
              {
                label: VARIABLE.NOT_HAVE,
                value: 0
              },
              {
                label: '2' + VARIABLE.PLACE,
                value: 1
              },
              {
                label: '4' + VARIABLE.PLACE,
                value: 2
              }
            ]
          }
        },
        {
          field: 'haveMonth',
          title: CONSTANTS_CODE.MONTH,
          width: '80',
          align: 'center',
          editRender: {
            name: '$select',
            options: [
              {
                label: VARIABLE.NOT_HAVE,
                value: false
              },
              {
                label: VARIABLE.HAVE,
                value: true
              }
            ]
          }
        },
        {
          field: 'haveDay',
          title: CONSTANTS_CODE.DAY,
          width: '80',
          align: 'center',
          editRender: {
            name: '$select',
            options: [
              {
                label: VARIABLE.NOT_HAVE,
                value: false
              },
              {
                label: VARIABLE.HAVE,
                value: true
              }
            ]
          }
        },
        {
          field: 'seed',
          title: CONSTANTS_CODE.SEED,
          minWidth: '130',
          align: 'center',
          editRender: { name: '$input', props: { type: 'number', min: VARIABLE.ONE_NUMBER, max: VARIABLE.BIG_NUMBER } }
        },
        {
          field: 'step',
          title: CONSTANTS_CODE.STEP,
          minWidth: '90',
          align: 'center',
          editRender: { name: '$input', props: { type: 'number', min: VARIABLE.ONE_NUMBER, max: VARIABLE.BIG_NUMBER } }
        },
        {
          field: 'suffix',
          title: CONSTANTS_CODE.SUFFIX,
          width: '160px',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'pipelineLength',
          title: CONSTANTS_CODE.PIPE_LINE_LENGTH,
          width: '120',
          align: 'center',
          editRender: { name: '$input', props: { type: 'number', min: VARIABLE.ONE_NUMBER, max: VARIABLE.BIG_NUMBER } }
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '100',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <i
                  onClick={() => this.copyRow(row, column)}
                  class="icon-table-addRow"
                ></i>,
                <i
                  onClick={() => this.deleteRow(row, column)}
                  class="icon-table-delete"
                ></i>
              ];
            }
          }
        }
      ]
    };
  },
  computed: {},
  created() {
  },
  mounted() {
    // 默认新增一行
    this.insertEvent();
  },
  methods: {
    /**
     * @description: 移除一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    deleteRow(row, column) {
      this.$XModal.confirm(MSG.CONFIRM_DELETE).then(type => {
        if (type === 'confirm') {
          this.$refs.xGrid.remove(row);
        }
      });
    },
    /**
     * @description: 执行新增一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    async insertEvent() {
      const record = {
        seed: 1,
        step: 1,
        pipelineLength: 1,
        haveMonth: 0,
        haveDay: 0,
        yearLength: 0

      };
      // 往表格插入临时数据，从指定位置插入一行或多行
      await this.$refs.xGrid.insertAt(record, -1);
    },
    async copyRow(row, column) {
      this.$refs.xGrid.clearActived();
      // const index = this.$refs.xGrid.$getRowIndex(row);
      const record = { ...row };
      delete record._XID;
      // 往表格插入临时数据，从指定位置插入一行或多行
      await this.$refs.xGrid.insertAt(record, row);
    },
    /**
     * @description: 表单提交
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async submit() {
      try {
        const {
          // 新增的数据
          insertRecords
        } = this.$refs.xGrid.getRecordset();

        const data = JSON.parse(JSON.stringify(insertRecords));
        const res = await mainCodeInsertBatchAction(
          {},
          data);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 返回主页面
          this.goBack();
          // 提示正常信息
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行验证
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async fullValidEvent() {
      const errMap = await this.$refs.xGrid
        .fullValidate()
        .catch(errMap => errMap);
      if (errMap) {
        var msgList = [];
        Object.values(errMap).forEach(errList => {
          errList.forEach((params, index) => {
            const { column, rules } = params;
            rules.forEach(rule => {
              msgList.push(
                `${VARIABLE.THE} ${index + 1} ${VARIABLE.ROW} ${column.title} ${MSG.VALIDATION_FAILED}：${rule.message}`
              );
            });
          });
        });
        this.$XModal.alert({
          status: 'error',
          message: () => {
            return [
              <div class="red" style="max-height: 400px;overflow: auto;">
                {msgList.map(msg => (
                  <div>{msg}</div>
                ))}
              </div>
            ];
          }
        });
      } else {
        // 通过验证，执行提交
        this.submit();
      }
    },
    /**
     * @description: 校验前缀字符串的方法
     * @param {object} rule 校验规则
     * @param {string} value input输入值
     * @param {function} callback 回调函数
     * @return: void
     */
    checkPrefix(rule, value, callback) {
      if (value && !/^[A-Z]{6}$/.test(value)) {
        callback(new Error(CONSTANTS_CODE.PREFIX_VALIDATION_FAILED));
      }
      callback();
    },

    /**
     * @description: 校验后缀字符串的方法
     * @param {object} rule 校验规则
     * @param {string} value input输入值
     * @param {function} callback 回调函数
     * @return: void
     */
    checkSuffix(rule, value, callback) {
      if (value && !/^[A-Z]{6}$/.test(value)) {
        callback(new Error(CONSTANTS_CODE.SUFFIX_VALIDATION_FAILED));
      }

      callback();
    },
    /**
     * @description: 返回按钮点击
     * @param 无
     * @return:无
     * @author: ZJM
     */
    goBack() {
      this.$emit('goBack', VARIABLE.ZERO_NUMBER);
    }
  }
};
